.education_container{
  display: grid;
  gap: 15%;
}

.education_cards{
  display: grid;
  /* grid-template-columns: repeat(3,1fr); */
  gap: 1.3rem;
}

.education_card{
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}

.education_card:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}