.projects_container{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  gap: 2.5rem;
}

.projects_item{
  /* background: var(--color-bg-variant); */
  border: var(--color-primary-variant);
  padding: 1.3rem;
  border-radius: 2rem;
  border: 1px solid var(--color-bg-variant);
  transition: var(--transition);
}

.projects_item:hover{
  border-color: var(--color-primary-variant);
  background: var(--color-bg-variant);
}

.projects_item-image{
  border-radius: 1.5rem;
  overflow: hidden;
}

.projects_item h3{
  margin: 1.2rem 0 2rem;
}

.projects_item-cta{
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
}


/*  media query for tablets (medium devices) */

@media screen  and (max-width : 1024px){
  .projects_container{
    grid-template-columns: 1fr 1fr;
    gap: 1.2rem;
  }
}


/*  media query for Mobiles (small devices) */

@media screen  and (max-width : 600px){
  .projects_container{
    grid-template-columns: 1fr;
    gap: 1rem;
  }
}