nav{
  background: white;
  width: max-content;
  display: block;
  padding: 0.5rem 1.1rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 1.5rem;
  display: flex;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
}

nav a{
  background: transparent;
  padding: 0.6rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-light);
  font-size: 1.2rem;
}

nav a:hover{
  background: rgba(0,0,0,0.3);
}

nav a.active{
  background: var(--color-primary);
  color: var(--color-bg);
}