header{
  height: 100vh;
  padding-top: 3rem;
  overflow: hidden;
}

.header_container{
  text-align: center;
  height: 100%;
  position: relative;
}

/* ********resume******* */

.resume{
  margin-top: 0.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}


/* ******socials****** */

.header_socials{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
  font-size: 2rem;
}

.header_socials::after{
  content: '';
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}


/* *******ME******** */
.me{
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 2rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 2rem 1.5rem 1.5rem 1.5rem;
}


/* ******scroll down****** */

.scroll_down{
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}



/*  media query for tablets (medium devices) */

@media screen  and (max-width : 1024px){
  header{
    height: 68vh;
  }
}


/*  media query for Mobiles (small devices) */

@media screen  and (max-width : 600px){
  header{
    height: 100vh;
  }
  .header_socials, .scroll_down{
    /* display: none; */
  }

  .me{
    width: 20rem;
    height: 25rem;
    position: absolute;
    left: calc(50% - 10rem);
    margin-top: 7rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 7rem 1.5rem 1.5rem 1.5rem;
  }

  .scroll_down{
    bottom: 8rem;
    font-size: 1rem;
  }

  .header_socials{
    bottom: 5rem;
    font-size: 1.5rem;
  }
  
  .header_socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
  }
}