.coding_container{
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}



.coding_cards{
  display: grid;
  grid-template-columns: repeat(5,1fr);
  gap: 3.7rem;
}

.coding_card{
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}

.coding_card:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.coding_item-image{
  width: 100%;
  height: 100%;
  border-radius: 20px;
}

.coding_item-image img{
  border-radius: 10px;
}

.webName2{
  display: none;
}
.webName1{
  display: contents;
}


/*  media query for tablets (medium devices) */

@media screen  and (max-width : 1024px){
  .coding_cards{
    grid-template-columns: 1fr 1fr 1fr;
    gap: 1.2rem;
  }


  .webName1{
    display: none;
  }
  .webName2{
    display: contents;
  }

}


/*  media query for Mobiles (small devices) */

@media screen  and (max-width : 600px){

  .coding_cards{
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }
  .coding_card{
    padding: 1rem;
  }

  .coding_content{
    text-align: center;
  }
  
  .webName1{
    display: none;
  }
  .webName2{
    display: contents;
  }

  
}